.landing {
    .cat-main-items {
        display: grid;
        grid-template-columns: repeat(auto-fill,calc(50% - 5px));
        justify-content: space-between;
        gap: 10px;
        .cat-item {
            position: relative;
            // cursor: pointer;
            overflow: hidden;
            .cat-image {
                img {
                    width: 100%;
                }
            }
            .cat-text {
                position: absolute;
                left: 0;
                bottom: 20px;
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                color: white;
                transition: .3s;
                transform: translateY(61px);
                h2 {
                    margin: 10px 0;
                    letter-spacing: 1px;
                    font-size: 30px;
                }   
                p {
                    margin: 0 0 20px;
                } 
                a {
                    padding: 10px 25px;
                    border: 1px solid white;
                    color: white;
                    transition: .3s;
                    border-radius: 10px;
                    &:hover {
                        border-color: red;
                        background-color: red;
                    }
                }            
            }
            &:hover {
                .cat-text {
                    transform: translateY(0);
                }
            }
        }
        @media (max-width: 600px) {
            grid-template-columns: 100%;
        }
    }
}