.reviews {
  padding-top: 50px;
  padding-bottom: 100px;
}

.reviews h2 {
  color: red;
  text-align: center;
  margin-bottom: 50px;
}

.reviews .container {
  position: relative;
  padding: 20px 10px;
  width: 100%;
}

.reviews .controls {
  position: absolute;
  top: -30px;
  right: 15px;
}

.reviews .controls span {
  font-size: 26px;
  cursor: pointer;
}

.reviews .controls span:hover {
  color: white;
  background-color: #777;
}

.reviews .all-review {
  display: -ms-grid;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 600px;
  gap: 30px;
  overflow-x: scroll;
  scroll-behavior: smooth;
}

.reviews .all-review::-webkit-scrollbar {
  display: none;
}

.reviews .all-review .review {
  background-color: #eee;
  padding: 20px;
  position: relative;
}

.reviews .all-review .review .rate {
  color: red;
}

.reviews .all-review .review p {
  line-height: 1.7;
}

.reviews .all-review .review h4 {
  margin-bottom: 10px;
  color: red;
}

.reviews .all-review .review > span {
  font-size: 14px;
}

.reviews .all-review .review::before {
  content: "";
  position: absolute;
  top: 2px;
  right: 2px;
  border: 40px solid white;
  border-left-color: transparent;
  border-bottom-color: transparent;
}
