.reviews {
    padding-top: 50px;
    padding-bottom: 100px;
    h2 {
        color: red;
        text-align: center;
        margin-bottom: 50px;
    }
    .container {
        position: relative;
        padding: 20px 10px;
        width: 100%;
    }
    .controls {
        position: absolute;
        top: -30px;
        right: 15px;
        span {
            font-size: 26px;
            cursor: pointer;
            &:hover {
                color: white;
                background-color: #777;
            }
        }
    }
    .all-review {
        display: grid;
        grid-auto-flow: column;
        grid-auto-columns: 600px;
        gap: 30px;
        overflow-x: scroll;
        scroll-behavior: smooth;
        &::-webkit-scrollbar {
            display: none;
        }
        .review {
            background-color: #eee;
            padding: 20px;
            position: relative;
            .rate {
                color: red;
            }
            p {
                line-height: 1.7;
            }
            h4 {
                margin-bottom: 10px;
                color: red;
            }
            >span {
                font-size: 14px;
            }
            &::before {
                content: "";
                position: absolute;
                top:2px;
                right: 2px;
                border: 40px solid white;
                border-left-color: transparent;
                border-bottom-color: transparent;
            }
        }
    }
}