.products-container .container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(220px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
}

.products-container .container .product {
  padding: 0 10px;
  text-align: center;
  position: relative;
}

.products-container .container .product .p-image {
  margin: 10px;
}

.products-container .container .product .p-image img {
  max-width: 100%;
}

.products-container .container .product .p-details {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.products-container .container .product .p-details .p-name {
  color: #191919;
  font-weight: bold;
  font-size: 20px;
  margin: 10px 0 5px;
}

.products-container .container .product .p-details .p-name a {
  color: inherit;
}

.products-container .container .product .p-details .p-price {
  margin-bottom: 10px;
}

.products-container .container .product .p-details .add-cart {
  background-color: white;
  border: 1px solid red;
  color: #191919;
  padding: 10px 20px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

.products-container .container .product .p-details .add-cart:hover {
  background-color: red;
  color: white;
}

.products-container .container .product .p-icons {
  position: absolute;
  top: 30px;
  right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 20px;
}

.products-container .container .product .p-icons span {
  background-color: #eee;
  color: #191919;
  font-weight: bold;
  font-size: 20px;
  padding: 10px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
}

.products-container .container .product .p-icons span:hover {
  background-color: red;
  color: white;
}

.products-container .container .product .p-icons span i {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
