.p-detailed-preview .container .preview-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.p-detailed-preview .container .preview-content .p-image {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  text-align: center;
}

.p-detailed-preview .container .preview-content .p-image img {
  max-width: 100%;
  max-height: 70vh;
}

.p-detailed-preview .container .preview-content .p-details {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  padding: 40px 30px 40px 10px;
}

.p-detailed-preview .container .preview-content .p-details .availabilty {
  font-weight: 500;
}

.p-detailed-preview .container .preview-content .p-details .availabilty span:first-child {
  color: #191919;
}

.p-detailed-preview .container .preview-content .p-details .availabilty span:last-child {
  color: #777;
}

.p-detailed-preview .container .preview-content .p-details .name-price h2 {
  color: #191919;
  margin-bottom: 0;
}

.p-detailed-preview .container .preview-content .p-details .name-price h4 {
  margin-top: 5px;
}

.p-detailed-preview .container .preview-content .p-details .p-description p {
  text-align: justify;
}

.p-detailed-preview .container .preview-content .p-details .add-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
  margin: 30px 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty {
  position: relative;
  width: 130px;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty input {
  height: 60px;
  width: 100%;
  text-align: center;
  border: none;
  background-color: #eee;
  font-size: 20px;
  border-radius: 8px;
  color: #777;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty input:focus {
  outline: none;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .minus-btn,
.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .plus-btn {
  position: absolute;
  color: #aaa;
  font-size: 24px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0 10px;
  height: 60px;
  cursor: pointer;
  top: 0;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .minus-btn:hover,
.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .plus-btn:hover {
  color: #777;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .minus-btn {
  left: 0;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .p-qty .plus-btn {
  right: 0;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .btn-add-cart {
  width: 60%;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .btn-add-cart button {
  background-color: red;
  color: white;
  font-weight: bold;
  font-size: 18px;
  padding: 0 50px;
  border-radius: 8px;
  border: none;
  height: 60px;
  -webkit-transition: .3s;
  transition: .3s;
}

.p-detailed-preview .container .preview-content .p-details .add-cart .btn-add-cart button:hover {
  background-color: white;
  border: 1px solid red;
  color: #191919;
}

.p-detailed-preview .container .preview-content .p-details .other-info {
  margin-bottom: 20px;
}

.p-detailed-preview .container .preview-content .p-details .other-info div span:first-child {
  color: #191919;
}

.p-detailed-preview .container .preview-content .p-details .more-details a {
  color: #191919;
}
