.p-detailed-preview {
.container {
    .preview-content {
        display: flex;
        align-items: center;
        .p-image {
            flex-basis: 50%;
            text-align: center;
            img {
                max-width: 100%;
                max-height: 70vh;
            }
        }
        .p-details {
            flex-basis: 50%;
            padding: 40px 30px 40px 10px;
            .availabilty {
                font-weight: 500;
                span {
                    &:first-child {
                        color: #191919;
                    }
                    &:last-child {
                        color: #777;
                    }
                }
            }
            .name-price {
                h2 {
                    color: #191919;
                    margin-bottom: 0;
                }
                h4 {
                    margin-top: 5px;
                }
            }
            .p-description {
                p {
                    text-align: justify;
                }
            }
            .add-cart {
                display: flex;
                gap: 20px;
                margin: 30px 0;
                align-items: center;
                .p-qty {
                    position: relative;
                    width: 130px;
                    input {
                        height: 60px; 
                        width: 100%;
                        text-align: center;
                        border: none;
                        background-color: #eee;
                        font-size: 20px;
                        border-radius: 8px;
                        color: #777;
                        &:focus {
                            outline: none;
                        }
                    }
                    .minus-btn,
                    .plus-btn {
                        position: absolute;
                        color: #aaa;
                        font-size: 24px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 0 10px;
                        height: 60px;
                        cursor: pointer;
                        top: 0;
                        &:hover {
                            color: #777;
                        }
                    }
                    .minus-btn {
                        left: 0;
                    }
                    .plus-btn {
                        right: 0;
                    }
                }
                .btn-add-cart {
                    width: 60%;
                    button {
                        background-color: red;
                        color: white;
                        font-weight: bold;
                        font-size: 18px;
                        padding: 0 50px;
                        border-radius: 8px;
                        border: none;
                        height: 60px;
                        transition: .3s;
                        &:hover {
                            background-color: white;
                            border: 1px solid red;
                            color: #191919;
                    }
                    }
                }
            }
            .other-info {
                margin-bottom: 20px;
                div {
                    span {
                        &:first-child {
                            color: #191919;
                        }
                    }
                }
            }
            .more-details {
                a {
                    color: #191919;
                }
            }
        }
    }
}}