.products-container {
    .container {
        display: grid;
        grid-template-columns: repeat(auto-fill,minmax(220px,1fr));
    .product {
        padding: 0 10px;
        text-align: center;
        position: relative;
        .p-image {
            margin: 10px;
            img {
                max-width: 100%;
            }
        }
        .p-details {
            display: flex;
            flex-direction: column;
            align-items: center;
            .p-name {
                color: #191919;
                font-weight: bold;
                font-size: 20px;
                margin: 10px 0 5px;
                a {
                    color: inherit;
                }
            }
            .p-price {
                margin-bottom: 10px;
            }
            .add-cart {
                background-color: white;
                border: 1px solid red;
                color: #191919;
                padding: 10px 20px;
                font-weight: bold;
                border-radius: 10px;
                cursor: pointer;
                transition: .3s;
                &:hover {
                    background-color: red;
                    color: white;
                }
            }
        }
        .p-icons {
            position: absolute;
            top: 30px;
            right: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            span {
                background-color: #eee;
                color: #191919;
                font-weight: bold;
                font-size: 20px;
                padding: 10px;
                border-radius: 50%;
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                &:hover {
                    background-color: red;
                    color: white;
                }
                i {
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
    }
    
}